@use '../layout-settings/colors' as colors;
@use '../layout-settings/dimensions' as dimensions;
@use '../layout-settings/fonts' as fonts;
@use '../layout-settings/wrapper';

$evenBackground: lighten(colors.$highlight, 20%);
$oddBackground: lighten(colors.$fontMeta, 40%);

.timeline {
  @extend %wrapper;
  color: colors.$highlight;
  position: relative;

  // line
  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: calc(dimensions.$spacing / 2);
    background: colors.$highlight;
    left: 20%;
    margin-left: calc(dimensions.$spacing * -1);
  }

  &__separator {
    margin-left: 25%;
    border-radius: dimensions.$border;
    padding: dimensions.$spacing;
    margin-bottom: dimensions.$spacing;
    @include fonts.size(2);
  }

  &__date {
    display: block;
    width: 15%;
    position: absolute;
    @include fonts.size(1.2);
  }

  &__headline {
    border-bottom: 1px solid colors.$font;
    padding-bottom: calc(dimensions.$spacing / 2);
    margin-bottom: calc(dimensions.$spacing / 2);
  }

  &__subheadline {
    color: colors.$fontMeta;
    margin-bottom: dimensions.$spacing;
  }

  &__description {
    margin-bottom: calc(dimensions.$spacing / 2);
    margin-left: calc(dimensions.$spacing / 2);
    padding-left: calc(dimensions.$spacing / 2);
    list-style-type: "\00BB";
  }

  &__content {
    background: $oddBackground;
    border-radius: dimensions.$border;
    padding: dimensions.$spacing;
    margin-left: 25%;
    position: relative;
    margin-bottom: dimensions.$spacing;

    .timeline__item:nth-of-type(even) & {
      background: $evenBackground;
    }

    // dot
    &:before {
      content: ' ';
      width: calc(dimensions.$spacing * 2);
      height: calc(dimensions.$spacing * 2);
      position: absolute;
      background: $evenBackground;
      box-shadow: 0 0 0 8px $oddBackground;
      border-radius: 50%;
      text-align: center;
      left: -12.5%;
      top: calc(dimensions.$spacing / 2);
      // animation
      animation-duration: 1s;
      animation-fill-mode: both;

      .timeline__item:nth-of-type(even) & {
        background: $oddBackground;
        box-shadow: 0 0 0 8px $evenBackground;
      }
    }

    // bouncing dot
    &:hover:before {
      animation-name: bounce;
    }

    // triangle
    &:after {
      right: 100%;
      border: solid transparent;
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      pointer-events: none;
      border-right-color: $oddBackground;
      border-width: calc(dimensions.$spacing / 2);
      top: dimensions.$spacing;

      .timeline__item:nth-of-type(even) & {
        border-right-color: $evenBackground;
      }
    }
  }
}


@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-20px);
  }

  60% {
    transform: translateY(-10px);
  }
}
